<template>
    <div >
        <!--头部 start--->
        <Header />
        <!--头部 end--->

        <!--中间内容 start--->
        <div class="content" >
            <div class="content-email">
                <div class="email-box">
                    <div class="email-logo"><img src="@/assets/imgs/email-logo.png" alt=""></div>
                    <div class="email-submit">
                        <p class="email-submit-title">Join our Community</p>
                        <p class="email-submit-desc">to receive the latest news and updates from the project!</p>
                        <div class="email-submit-input">
                            <input type="text" placeholder="Enter your email">
                        </div>
                        <div class="email-submit-input">
                            <input type="text" placeholder="Enter your name">
                        </div>
                        <p class="email-submit-subTitle">I consider myself a...</p>
                        <div class="email-submit-sex">
                            <div class="sex-txt">
                                <img src="@/assets/imgs/email-boy.png" alt="">
                                <p>Boy</p>
                            </div>
                            <div class="sex-txt">
                                <img src="@/assets/imgs/email-girl.png" alt="">
                                <p>Girl</p>
                            </div>
                        </div>
                        <p class="email-submit-agree"></p>
                        <p class="email-submit-button"></p>
                    </div>
                </div>
            </div>
        </div>
        <!--中间内容 end--->
    </div>
</template>
<script>
import Header from '@/components/header'
import Footer from '@/components/footer'
import Menu from '@/components/menu'
export default {
    name: 'Email',
    components:{
        Header,
        Footer,
        Menu
    },
    data(){
        return {
            activeName:'1'
        }
    }
}
</script>
<style scoped lang="less">

</style>



